import {Injectable} from 'angular2/core';
import {Tab} from '../interfaces/tab';

@Injectable()
export class TabService
{
    private _tabs: Tab[] = [
        {
            "name": "Tab 1",
            "content": "Hi!"
        }
    ];

    getTabs(): Tab[] {
        return this._tabs;
    }

    addTab(tab: Tab) {
        this._tabs.push(tab);
    }

    removeTab(tab: Tab) {
        var index: number = this._tabs.indexOf(tab);
        if (index > -1) {
            this._tabs.splice(index, 1);
        }
    }
}
